import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchMoreImages } from '../actions/searchAction';
import { fetchPage } from '../actions/searchAction';

import { GridList, GridTile } from 'material-ui/GridList';
import IconButton from 'material-ui/IconButton';
import ZoomIn from 'material-ui/svg-icons/action/zoom-in';
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';
import RefreshIndicator from 'material-ui/RefreshIndicator';

// hook for correct paddingTop when opening Dialog with dynamic content; correct Dialog display for 'portrait'&'landskape':
const dialogStyle = {
  dialogRoot: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    paddingTop: 0
  },
  dialogContent: {
    position: 'relative',
    width: 'auto',
    maxWidth: '95vw',
    transform: 'translate(0px, 0px)' 
  },
  dialogBody: {
    padding: 2,
    alignText:'center',
    objectFit: 'contain',
  },
  // my additional settings
  dialogImg: {
    maxWidth: '94vw',
    maxHeight: '90vh',
  },
};

class ImageResults extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // bottomReached: false,
      open: false,
      currentImg: '',
      currentArrInd: '', 
      // showRefreshIndicator: false,
    };
    this.handleScroll = this.handleScroll.bind(this);
  }
  
  addScrollListener() {
    window.addEventListener('scroll', this.handleScroll);
  }

  removeScrollListener() {
    window.removeEventListener('scroll', this.handleScroll)
  }

  handleScroll() {
    const body = document.body;
    const html = document.documentElement;

    // get height of the window 
    const windowHeight = "innerHeight" in window ? window.innerHeight : document.documentElement.offsetHeight;
    // calculate whole document height
    const docHeight = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight);
    // calculate bottom of window
    const windowBottom = windowHeight + window.pageYOffset;
    // if window bottom is greater than or equal to document height then user reached to bottom
    if (windowBottom >= docHeight) {
      console.log('bottom reached', this.props);
      this.props.fetchPage(this.props.page + 1);
      this.props.fetchMoreImages();
      this.removeScrollListener();
    } else {
      console.log('not at bottom')
    }
  };

  handleImgOpen = (img, index) => {
    this.setState({ open: true, currentImg: img.largeImageURL, currentArrInd: index});
  }

  handleImgClose = () => {
    this.setState({ open: false });
  }
  
  handleNextButton = (side) => {
    let nextArrInd;
    if (side === 'right'){
      nextArrInd = this.state.currentArrInd + 1
    } else {
      nextArrInd = this.state.currentArrInd - 1
    }

    if (( nextArrInd > -1 ) && ( nextArrInd < ( this.props.images.length ) ) ) {
      this.handleImgOpen(this.props.images[nextArrInd], nextArrInd) 
    } else {
      this.handleImgClose()
    }
  }  
  
  render() {
    let imageListContent;
    let cellHeight;

    const { images, totalHits, page } = this.props;
    console.log(this.props);
    console.log('ImageResults, this.props.designType', this.props.designType);

    const styles = {
      gridListIMG: {
        // width: '50%',
        // height: 'auto',
        // height: '100%',
      },
      gridList: {
        margin: 0,
      },
    }

    const gridCols = () => {
      console.log('gridCols', window.innerWidth);
      if(this.props.orientation === 'horizontal') {
        cellHeight = 180;
        dialogStyle.dialogContent.width = 'auto';
        dialogStyle.dialogContent.maxWidth = '95vw';

        dialogStyle.dialogContent.height = 'auto';
        dialogStyle.dialogContent.maxheight = 'auto';

        dialogStyle.dialogBody.maxWidth = '95vw';
        dialogStyle.dialogBody.maxHeight = 'auto';

        // dialogStyle.dialogImg.width = '100%';
        // dialogStyle.dialogImg.height = 'auto';

        switch (true) {
          case (this.props.designType === 1):
            return 5
            break;
          case (this.props.designType === 2):
            return 4
            break;
          case (this.props.designType === 3):
            return 3
            break;
          case (this.props.designType === 4):
            return 2
            break;
          default:
            return 1
        }
      } else {
        cellHeight = 270;

        dialogStyle.dialogContent.maxHeight = '100vh';

        dialogStyle.dialogContent.width = 'auto';
        dialogStyle.dialogContent.maxWidth = 'auto';
        
        dialogStyle.dialogBody.maxHeight = '90vh';

        // dialogStyle.dialogImg.width = 'auto';
        // dialogStyle.dialogImg.height = '100%';
        // dialogStyle.dialogImg.maxHeight = '90vh';

        switch (true) {
          case (this.props.designType === 1):
            return 8
            break;
          case (this.props.designType === 2):
            return 6
            break;
          case (this.props.designType === 3):
            return 4
            break;
          case (this.props.designType === 4):
            return 3
            break;
          default:
            return 2
        }
      }

    }

    if(images) {
      imageListContent = (
        <GridList cols={gridCols()} cellHeight={cellHeight} style={styles.gridList}>
          {images.map((img, index) => (
            <GridTile
              key={img.id}
              title={img.tags}
              subtitle={<span>by <strong>{img.user}</strong></span>}
              actionIcon={
                <IconButton onClick={() => this.handleImgOpen(img, index)}>
                  <ZoomIn color='white' />
                </IconButton>
              }
            >
              <img src={img.webformatURL} alt={img.tags} style={styles.gridListIMG} className='gridIMG' />
            </GridTile>
          ))}
        </GridList>
      )
    } else {
      imageListContent = null
    }

    if(totalHits > images.length) {
      this.addScrollListener();
      // this.setState({
      //   showRefreshIndicator: true,
      // })
    } else {
      console.log('totalHits < images.length')
    }

    if(page===1) {
      window.scrollTo(0, 0)
    }

    const actions = [
      <FlatButton
        label='Left'
        primary={true}
        onClick={this.handleNextButton}
      />,
      <FlatButton
        label='Right'
        primary={true}
        onClick={() => this.handleNextButton('right')}
      />, 
      <FlatButton
        label = 'Close'
        primary = {true}
        onClick = {this.handleImgClose}
      />
    ]

    return (
      <div>
        {imageListContent}
        {/* {this.state.showRefreshIndicator ? <RefreshIndicator />: null} */}
        <Dialog
          actions = {actions}
          modal = {false}
          open = {this.state.open}
          onRequestClose = {this.handleImgClose}
          contentStyle={dialogStyle.dialogContent}
          bodyStyle={dialogStyle.dialogBody}
          style={dialogStyle.dialogRoot}
          repositionOnUpdate={false}

          autoDetectWindowHeight={false}
        >
          {/* <div className='dialogIMGContainer'> */}
            <img className='dialogIMG' src={this.state.currentImg} alt='' style={dialogStyle.dialogImg} />
          {/* </div> */}
        </Dialog>
      </div>
    )
  }
}

ImageResults.propTypes = {
  fetchMoreImages: PropTypes.func,
  fetchPage: PropTypes.func,
  images: PropTypes.array,
  totalHits: PropTypes.number,
  page: PropTypes.number,
  designType: PropTypes.number,
  orientation: PropTypes.string,
}

const mapStateToProps = state => ({
  images: state.search.images,
  totalHits: state.search.total,
  page: state.search.page,
  designType: state.search.designType,
  orientation: state.search.orientation,
})

export default connect(mapStateToProps, { fetchMoreImages, fetchPage })(ImageResults)


